
@font-face {
  font-family: "Montserrat";
  src: url('../../../fonts/regular/Montserrat-Regular.woff2'),
    url('../../../fonts/regular/Montserrat-Regular.woff'),
    url('../../../fonts/regular/Montserrat-Regular.ttf'),
    url('../../../fonts/regular/Montserrat-Regular.svg');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../../../fonts/bold/Montserrat-SemiBold.woff2"),
  url("../../../fonts/bold/Montserrat-SemiBold.woff"),
  url("../../../fonts/bold/Montserrat-SemiBold.ttf"),
  url("../../../fonts/bold/Montserrat-SemiBold.svg");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Quicksand";
  src: url("../../../fonts/regular/Quicksand-Regular.woff2"),
    url("../../../fonts/regular/Quicksand-Regular.woff"),
    url("../../../fonts/regular/Quicksand-Regular.ttf"),
    url("../../../fonts/regular/Quicksand-Regular.svg");
  font-weight: 400;
  font-style: normal;
}
  
@font-face {
  font-family: "Quicksand";
  src: local('Quicksand'),
    local('Quicksand-SemiBold'),
    url('../../../fonts/bold/Quicksand-SemiBold.woff2') format('woff2'),
    url('../../../fonts/bold/Quicksand-SemiBold.woff') format('woff'),
    url('../../../fonts/bold/Quicksand-SemiBold.ttf') format('truetype'),
    url('../../../fonts/bold/Quicksand-SemiBold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Material Icons";
  font-style: normal;
  font-weight: 400;
  src: local('Material Icons'),
    local('MaterialIcons-Regular'),
    url('../../../fonts/material-icons/MaterialIcons-Regular.woff2') format('woff2'),
    url('../../../fonts/material-icons/MaterialIcons-Regular.woff') format('woff'),
    url('../../../fonts/material-icons/MaterialIcons-Regular.ttf') format('truetype'),
    url('../../../fonts/material-icons/MaterialIcons-Regular.svg') format('svg');

}

